.divCartVoid {
    display: inline-block;
    align-items: center;
    justify-content: center;
    align-content: center;
    height: 280px;
    width: 600px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    padding-top: 10px;
    margin-bottom: 0px;
    padding-left: 5px;
    padding-right: 5px;
    border-color: gray;
    background: linear-gradient(90deg, rgba(148, 82, 7, 0.23) 0%, rgba(21, 129, 6, 0.24) 50%, rgba(148, 82, 7, 0.23) 100%);
    border-width: 2px 2px 2px 2px;
    border-radius: 20px 20px 20px 20px;
    -webkit-box-shadow: 3px 3px 8px 2px #0d7a12;
    box-shadow: 3px 3px 8px 2px #0d5a11;
  }
  .divCartActions {
    display: inline-block;
    align-items: center;
    justify-content: center;
    align-content: center;
    height: 280px;
    width: 500px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    padding-top: 10px;
    margin-bottom: 0px;
    padding-left: 5px;
    padding-right: 5px;
    border-color: gray;
    /* background: linear-gradient(90deg, rgba(148, 82, 7, 0.23) 0%, rgba(21, 129, 6, 0.24) 50%, rgba(148, 82, 7, 0.23) 100%);
    border-width: 2px 2px 2px 2px;
    border-radius: 20px 20px 20px 20px;
    -webkit-box-shadow: 3px 3px 8px 2px #0d7a12;
    box-shadow: 3px 3px 8px 2px #0d5a11; */
  }
  .btnCartBack {
    background-color: #0d5a1196;
    font-weight: 400;
    text-decoration: 'none';
    list-style: 'none';
    color: #1a1f21;
    width: auto;
    height: 1.5em;
    font-size: 18px;
    margin-top: -8px;
    margin-left: 5px;
    padding-top: -12px;
    border-radius: 5px 5px 5px 5px;
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    border-top: 1px solid gray;
  }
  .btnCartBack:hover{
    transform: scale(1.1, 1.1);
    transition: all 300ms ease-in-out;
 }
 .btnCart {
    background-color: #0d5a1196;
    font-weight: 400;
    text-decoration: 'none';
