  }

  .modalEndBuy {
    background: linear-gradient(90deg, rgba(148, 82, 7, 0.53) 0%, rgba(21, 129, 6, 0.54) 50%, rgba(148, 82, 7, 0.53) 100%);
    border-width: 2px 2px 2px 2px;
    border-radius: 20px 20px 20px 20px;
    -webkit-box-shadow: 3px 3px 8px 2px #0d7a12;
    box-shadow: 3px 3px 8px 2px #0d5a11;
  }

  .dataInput{
      margin-top: 2px;
      font-size: 18px;
